.profile {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media screen and (max-width: 930px) {
  .profile {
    padding: 0 19px 0;
  }
}

@media screen and (max-width: 768px) {
  .profile {
    flex-direction: column;
    justify-content: flex-start;
  }
}
